.hg-button.hg-functionBtn.hg-button-ctrl {
  max-width: 10%;
}

html {
  background-color: black;
}

.suggestions-menu {
  position: absolute;
  right: 0;
  top: 0;
  width: 5%;
  height: 100%;
  text-align: center;
  color: white;
}

.suggestions-menu > div {
  height: 36px;
  width: 100%;
  line-height: 36px;
}

.suggestion-area .suggestions-menu .prev,
.suggestion-area .suggestions-menu .next,
.suggestion-area .suggestions-menu .pagination {
  display: none;
}
.suggestion-area.expanded .suggestions-menu .prev,
.suggestion-area.expanded .suggestions-menu .next,
.suggestion-area.expanded .suggestions-menu .pagination {
  display: block;
}

.suggestions-menu .disabled {
  color: grey;
}

.mgto-keyboard-theme {
  background-color: #0a164c;
  border-radius: 0;
  width: 100vw;
}

.mgto-keyboard-theme .hg-row .hg-button {
  background-color: #001969;
  border: none;
  border-radius: 0;
  color: white;
}

.mgto-keyboard-theme .preview-pinyin {
  color: grey;
}
